import React from 'react';
import './setup.css'; 
import './App.css';
import './listings.css';
import './index.css';


import firebase from './firebase'
import Header from './header.js'
import Button from './button.js'




class App extends React.Component {
  
  constructor() {
    super();
    this.state = {
      listings: [],
      filteredListings: []
    }
  }

  filterListings = (event, userSelection) => {
    const copyofListings = [...this.state.listings];

    const filteredListings = copyofListings.filter( (listings) => {
      return listings[userSelection]
    })
    this.setState({
      filteredListings
    })

  }

  componentDidMount() {

    // Here we create a variable that holds a reference to our database
    const dbRef = firebase.database().ref();
    dbRef.on('value', (response) => {
      const listings = [];
      const data = response.val();

      for (let key in data) {
        listings.push(data[key]);
      }

      this.setState({
        listings
      });
    });
  }
  
  render() {
    return (
      <div className="App">

        <Header />
        <Button handleFilter={this.filterListings} />
        <div className="listingsContainer">
          <ul>
            {this.state.listings.map((listings) => {
              return (
                <li key={listings.id}>
                  <img src={listings.img1}></img>
                  <p>{listings.address}</p>
                  <p>{listings.city}, ON</p>
                  <p>Bedrooms: {listings.bedrooms}</p>
                  <p>${listings.price}</p>
                  <p>{listings.neighbourhood}</p>
                </li>
              )
            })}
          </ul>
        </div>
      </div>
    )
  }
}

export default App;
